.g-sidebar-dialog-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  //遮罩
  > .cover {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: gray;
    opacity: 0.4;
  }
  .sidebar-dialog {
    position: absolute;
    top: 0;
    right: -360px;
    height: 100%;
    width: 360px;
    background: white;
    transition: right 0.5s;
    &.sidebar-fade-in {
      right: 0;
    }
    &-header {
      z-index: 1;
      position: absolute;
      width: 100%;
      font-size: 1rem;
      line-height: 2rem;
      padding: 6px 0;
      background: #dadedd;
      .btn-back {
        position: absolute;
        top: 6px;
        left: 0;
        padding: 0 10px;
        cursor: pointer;
        background-color: #cccccc;
        border-radius: 100% / 50% 50% 50% 0;
        &:hover {
          color: #5cb85c;
        }
      }
      .title {
        text-align: center;
      }
    }
    &-content {
      padding: 44px 0 0 0;
      height: 100%;
      overflow: auto;
      .g-sidebar-dialog-tips {
        padding: 200px 0 0 0;
        text-align: center;
      }
    }
  }
}


